/* Reset Start */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
  scroll-behavior: smooth;
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Reset End */
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.scroll-hiden {
  overflow: hidden;
}
