  font-weight: 400;
  line-height: 18px;
}

.footer-form {
  flex-direction: column;
  gap: 14px;
  display: flex;
}

.footer-imput-form {
  border: 1px solid var(--primary-cl);
  color: var(--primary-cl);
  background-color: rgba(0, 0, 0, 0);
